import React from "react";
import { Grid } from "@material-ui/core";
import { Background, Body } from "./Layouts";
import { FloatingCard, ExtraCard } from "./Components";
import JsonData from "./Assets/data.json";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.state = { index: 0 };
  }

  isEmptyObj(obj) {
    return !Object.keys(obj).length;
  }

  next() {
    const { index } = this.state;

    if (index + 1 >= JsonData.length)
      this.setState({ index: JsonData.length - 1 });
    else this.setState({ index: index + 1 });
  }

  previous() {
    const { index } = this.state;

    if (index - 1 <= 0) this.setState({ index: 0 });
    else this.setState({ index: index - 1 });
  }

  render() {
    return (
      <React.Fragment>
        <Background />
        <Body>
          <Grid item xs={3} style={{ zIndex: -1 }}>
            <ExtraCard
              data={JsonData[this.state.index].extraCard}
              isEnabled={!this.isEmptyObj(JsonData[this.state.index].extraCard)}
            />
          </Grid>
          <Grid item xs={4}>
            <FloatingCard
              JsonData={JsonData[this.state.index]}
              next={this.next}
              isNextEnabled={this.state.index < JsonData.length - 1}
              previous={this.previous}
              isPreviousEnabled={this.state.index > 0}
            />
          </Grid>
        </Body>
      </React.Fragment>
    );
  }
}
