/* eslint-disable import/no-anonymous-default-export */
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Card, CardContent, Button, Typography } from "@material-ui/core";
import { red } from "@material-ui/core/colors";
import RoomIcon from "@material-ui/icons/Room";
import ReactPlayer from "react-player";
import FlyVideo0 from "../Assets/video/fly0.mp4";
import FlyVideo1 from "../Assets/video/fly1.mp4";

const styles = makeStyles({
  root: {
    //minHeight: "32.5em",
    minHeight: "30em",
    transition: "all 300ms linear",
    transform: "translateX(120%) scale(0.1)",
  },
  mapIcon: {
    marginRight: "0.3em",
    marginLeft: "0.2em",
    transform: "scale(1.2)",
    color: red[900],
  },
});

function getVideoFromId(id) {
  switch (id) {
    case 0:
      return FlyVideo0;
    case 1:
      return FlyVideo1;
    default:
      return null;
  }
}

export default (props) => {
  const classes = styles();

  let style = {
    opacity: props.isEnabled ? 1 : 0,
    transform: props.isEnabled
      ? "translateX(0) scale(1)"
      : "translateX(120%) scale(0.1)",
  };

  const video = getVideoFromId(props.data.videoId);

  return (
    <Card className={classes.root} style={style}>
      <CardContent>
        <Typography
          variant="h5"
          component="h2"
          style={{ marginBottom: "0.25em" }}
        >
          <RoomIcon className={classes.mapIcon} />
          {props.data.title}
        </Typography>
        <Button
          disabled={!props.isEnabled}
          onClick={() => window.open(props.data.link, "_blank")}
        >
          <ReactPlayer
            url={video}
            loop={true}
            playing={true}
            muted={true}
            pip={false}
            width="100%"
            height="auto"
          />
        </Button>
      </CardContent>
    </Card>
  );
};
